













































import _ from "lodash-es";
import store from "@/store";
import { Message } from "element-ui";
import formAttrDefault from "@/store/formAttrDefault";
import {
  defineComponent,
  ref,
  computed,
  watch,
  toRefs
} from "@vue/composition-api";

export default defineComponent({
  name: "importDialog",
  props: {
    visible: {
      type: Boolean,
      default: false
    }
  },
  setup(props, context) {
    const isShowPreview = ref(false);
    const formDesc = ref({});
    const formAttr = ref({});
    const jsonData = ref({} as AnyObj);
    const jsonStr = ref("{}");
    const isError = ref(false);

    // , 
    const importDisabled = computed(() => {
      return !jsonData.value || Object.keys(jsonData.value).length === 0;
    });

    // , 
    const { visible } = toRefs(props);
    watch(visible, () => {
      isShowPreview.value = false;
      jsonData.value = {};
      formDesc.value = {};
      jsonStr.value = "{}";
    });

    // 
    const handleChange = _.debounce(json => {
      try {
        json = eval("(" + json + ")");
        if (typeof json === "object") {
          jsonData.value = _.cloneDeep(json);
          formDesc.value = jsonData.value.formDesc;
          isError.value = false;
        } else {
          isError.value = true;
        }
      } catch (err) {
        isError.value = true;
      }
    }, 200);

    // json
    const handleImport = () => {
      if (typeof jsonData.value !== "object" || isError.value) {
        Message.error(" JSON ");
        return false;
      }

      if (jsonData.value && jsonData.value.formDesc) {
        const keys = Object.keys(formAttrDefault);
        formAttr.value = Object.assign(
          formAttrDefault,
          _.pick(jsonData.value, keys)
        );

        //  formAttr
        isShowPreview.value = true;
      } else {
        Message.error(' "formDesc" ');
        return false;
      }
    };

    // jsonjson  list)
    function json2Form() {
      // list 
      store.commit("clearCurrentForm");
      // 1. formAttr 
      store.commit("updateCurrentFormAttr", formAttr.value);
      // 2. formDesc
      // 3. formDesc.attr
      const list = Object.entries(formDesc.value).map(([key, val]) => ({
        ...(val as object),
        attrs: (val as { attrs: AnyObj }).attrs || {},
        field: key
      }));
      //  list
      store.commit("updateCurrentFormItemList", list);
      //  selectIndex
      store.commit("updateFormItemIndex", 0);
    }

    // 
    function confirmGen() {
      json2Form();
      context.emit("update:visible", false);
    }
    return {
      formData: ref({}),
      confirmGen,
      handleImport,
      handleChange,
      importDisabled,
      isShowPreview,
      formDesc,
      formAttr,
      jsonData,
      jsonStr,
      isError
    };
  },
  methods: {
    handleRequest(data) {
      // eslint-disable-next-line no-console
      console.log(data);
      return Promise.resolve();
    },
    handleRequestSuccess() {
      this.$message.success("");
    }
  }
});
